.offerings__btn, .hero__btn, .header__btn {
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.hero__btn, .header__btn {
  min-width: 100px;
  max-width: 106px;
  height: 32px;
  background-color: var(--btn-cl);
  text-align: center;
  color: inherit;
  cursor: pointer;
  border: none;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  transition-property: background-color;
  display: flex;
}

.hero__btn:hover, .header__btn:hover {
  background-color: var(--accent-btn-cl);
}

.menu-overlay {
  z-index: 1000;
  width: 130px;
  height: 255px;
  background-color: var(--heder-bcg-cl);
  box-shadow: rgba(0, 0, 0, .06);
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transition: opacity var(--anim), visibility var(--anim);
  border-radius: 30px 0 30px 30px;
  position: fixed;
  top: 50px;
  right: 20px;
}

@media screen and (min-width: 768px) {
  .menu-overlay {
    display: none;
  }
}

.menu-overlay.is-open {
  opacity: 1;
  pointer-events: initial;
  visibility: visible;
}

.menu {
  transition: transform var(--anim);
  transform: translateX(100%);
}

@media screen and (min-width: 768px) {
  .menu {
    display: none;
  }
}

.menu-overlay.is-open .menu {
  transform: translateX(0);
}

